.craousel {
    /* display: none; */
    border: 0px solid white;
  }
  @media screen and (width <= 280px) {
    .craousel {
      width: 190px;
    }
  }
  
  /* ['270px','400px','600px','750px','500px'] */
  @media screen and (280px<width <= 320px) {
    .craousel {
      width: 270px;
    }
  }
  @media screen and (320px< width <= 413px) {
    .craousel {
      width: 280px;
    }
  }
  @media screen and (413px< width <= 520px) {
    .craousel {
      width: 310px;
    }
  }
  @media screen and (520px< width < 600px) {
    .craousel {
      width: 400px;
    }
  }
  @media screen and (600px< width < 768px) {
    .craousel {
      width: 500px;
    }
  }
  
  @media screen and (768px<= width <= 960px) {
    .craousel {
      width: 630px;
    }
  }
  @media screen and (960px < width <= 1200px) {
    .craousel {
      width: 750px;
    }
  }
  
  @media screen and (1200px < width <= 1536px) {
    .craousel {
      width: 470px;
    }
  }
  

  .slick-prev, .slick-next {
    z-index: 1; /* Make sure the buttons are on top of the slides */
    width: 40px; /* Set the width of the buttons */
    height: 40px; /* Set the height of the buttons */
    top: 50%; /* Position the buttons vertically in the middle */
    transform: translateY(-50%); /* Center the buttons */
    background-color: transparent; /* Make the background transparent */
    border: none; /* Remove the border */
    outline: none; /* Remove the outline */
  }
  
  .slick-prev {
    left: 5px; 
    /* Position the previous button on the left */
  }
  
  .slick-next {
    right: 5px; 
    /* Position the next button on the right */
  }
  
  @media (max-width: 768px) {
    /* Adjust the position of the buttons for smaller screens */
    .slick-prev, .slick-next {
      width: 30px;
      height: 30px;
      font-size: 0;
    }
    
    .slick-prev {
      left: 5px;
    }
  
    .slick-next {
      right: 5px;
    }
  }
  